<template>
  <div class="billsResult reportTable">
    <v-container
      fluid
      grid-list-xl>

      <!-- 標題 -->
      <CommonBtnBack
        :Title="(mapVendorItems[gameHallItems.selected]) ? $ln(mapVendorItems[gameHallItems.selected]) + $ln('注單明細') : $ln('注單明細')"
        :toBack="toBackReport"
      />

      <!-- 投注時間 -->
      <v-layout
        row
        wrap
        class="pl-1">
        <v-flex
          md9
          xs12>
          {{ $ln('投注時間') }}：<br>{{ searchInfo.showTime }}
        </v-flex>

        <!-- 每秒更新 -->
        <CommonCounter
          :reciprocalItems.sync="reciprocalItems"
          :updateTableList="updateTableList"
        />
      </v-layout>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else>

        <!-- 展開搜尋條件開關 -->
        <ReportSearchInfoCollapse
          :searchInfo.sync="searchInfo"
        />

        <div class="pl-1">
          <!-- 展開搜尋條件內容 -->
          <ReportSearchInfoContent
            :searchInfo.sync="searchInfo"
          />

          <!-- 館別 -->
          <CommonRadio
            :TypeItems.sync="gameHallItems"
            :Title="$ln('館別')"
          />

          <!-- 顯示 -->
          <CommonCheckbox
            :TypeItems.sync="stratumItems"
            :TypeSelectAll.sync="stratumSelectAll"
            :Title="$ln('顯示')"
            TitlePos="top"
          />

          <!-- 會員帳號 -->
          <div>
            <label class="body-1 theme--light">{{ $ln('會員帳號') }}</label>
            <span class="pl-2">
              <span class="badge badge-border">
                {{ searchInfo.account }}<span v-if="searchInfo.nickname">({{ searchInfo.nickname }})</span>
              </span>
            </span>
          </div>
        </div>

        <!-- 搜尋列表 -->
        <ReportTableBill
          :List.sync="tableList"
          :SelectItems.sync="stratumItems"
          :GameHallSelected.sync="gameHallItems.selected"
          :mapVendorItems="mapVendorItems"
          :loadding.sync="lazy.loading"
        />

      </div>
    </v-container>

  </div>
</template>

<script>
import { common } from '@/utils'
import _report from '@/utils/api/report'
import BillResult from '@/mixins/report/BillResult'

export default {
  name: 'TimeReportBillsResult',
  mixins: [BillResult],
  data () {
    return {
      // 館別 (pe體彩, lt彩球, ry皇家, bg賓果)
      gameHallItems: {
        selected: 'pe,bg,lt,ry',
        options: [
          {
            text: '全部',
            value: 'pe,bg,lt,ry',
            type: 'all',
            show: true
          },
          {
            text: '全部(不包含Super彩球)',
            value: 'pe,bg,ry',
            type: 'allNoLottery',
            show: true
          },
          {
            text: '體彩',
            value: 'pe',
            type: 'sport',
            show: true
          },
          {
            text: 'BingoStar',
            value: 'bg',
            type: 'bingo',
            show: true
          },
          {
            text: 'Super彩球',
            value: 'lt',
            type: 'lottery',
            show: true
          },
          {
            text: '皇家',
            value: 'ry',
            type: 'royal',
            show: true
          }
        ]
      },
      searchItems: {
        page: 1 ,
        // 報表類型(cur當日,his歷史,time時間,post未匯帳)
        type: 'time', // API 請求參數直接設置
        // 帳務日期起
        time_start: '',
        // 帳務日期迄
        time_end: '',
        // 館別
        vendor: '',
        // 會員帳號
        account: '',
        // [體彩]館別/項目
        ball_type: '',
        // [體彩]類型
        game_type: '',
        // [體彩]玩法
        play_type: '',
        // [彩球]館別/項目
        lt_game: '',
        // [賓果]館別/項目
        bg_game: '',
        // [皇家]館別/項目
        ry_game: ''
      },
      // 報表路徑
      reportUrl: '/manage/report/time-report/'
    }
  },
  mounted () {
    this.updateDashboardInfo()

    // console.log('Get bills: ', this.$route.query.searchItems, 'account: ', this.$route.query.account)
    if (common.isObject(this.$route.query.searchItems)) {
      // 查詢結果有前一頁傳過來這邊不另外處理
      this.searchInfo = this.$route.query.searchInfo
      // 帳務日期起
      this.searchItems.time_start = this.$route.query.searchItems.time_start
      // 帳務日期迄
      this.searchItems.time_end = this.$route.query.searchItems.time_end
      // 會員帳號
      this.searchItems.account = this.$route.query.account
      this.searchInfo.account = this.$route.query.account
      // 會員暱稱
      this.searchInfo.nickname = this.$route.query.nickname
      // [體彩]館別/項目
      this.searchItems.ball_type = this.$route.query.searchItems.ball_type
      // [體彩]類型
      this.searchItems.game_type = this.$route.query.searchItems.game_type
      // [體彩]玩法
      this.searchItems.play_type = this.$route.query.searchItems.play_type
      // 取得返回上層報表的搜尋帳號
      this.getBackAccount(this.$route.query.account, this.$route.query.level, this.$route.query.searchTree)
      // 層級線
      this.searchItems.search_tree = this.$route.query.searchTree
      // 欲搜尋館別
      this.searchVendorItems = this.$route.query.searchVendorItems

      // 開關館別選項
      this.switchVendor()

      // 館別
      this.searchItems.vendor = this.$route.query.searchItems.vendor
      this.gameHallItems.selected = this.$route.query.searchItems.vendor
      if (this.$route.query.hasOwnProperty('vendorCode')) {
        // console.log('Get bills: ', this.$route.query.searchItems, 'vendorCode: ', this.$route.query.vendorCode)
        this.searchItems.vendor = this.$route.query.vendorCode
        this.gameHallItems.selected = this.$route.query.vendorCode
      }
      this.lazy.vendor = (this.searchItems.vendor).split(',')

      // 組合開合搜尋條件資料
      this.composeSearchInfo(this.$route.query.searchItems, this.gameHallItems.selected)
    } else {
      // 無物件資料，則轉跳上一頁
      this.toBack()
    }
  },
  methods: {
    // 取得會員注單明細(X使用懶加載這函式不使用X)
    loadBillsResult () {
      try {
        // console.log(`時間會員注單明細搜尋項目:`, this.searchItems, `館別:`, this.searchItems.vendor, `層級樹:`, JSON.stringify(this.searchItems.search_tree))
        this.getBillsResult(this.searchItems)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得會員注單明細失敗`))
              console.error(res.response)
              // 跳轉回搜尋頁面
              this.toBack()
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
                // 跳轉回搜尋頁面
                this.toBack()
              } else if (res.data.hasOwnProperty('result')) {
                // console.log('取得會員注單明細: ', res.data.result)
                let items = []
                // 重組資料
                if (res.data.result.hasOwnProperty('pe')) { // 體彩一般單
                  for (let i in res.data.result.pe) {
                    res.data.result.pe[i].vendor = 'pe'
                    items.push(res.data.result.pe[i])
                  }
                }
                if (res.data.result.hasOwnProperty('bg')) { // 賓果單
                  for (let i in res.data.result.bg) {
                    res.data.result.bg[i].vendor = 'bg'
                    items.push(res.data.result.bg[i])
                  }
                }
                if (res.data.result.hasOwnProperty('lt')) { // Super彩球單
                  for (let i in res.data.result.lt) {
                    res.data.result.lt[i].vendor = 'lt'
                    items.push(res.data.result.lt[i])
                  }
                }
                if (res.data.result.hasOwnProperty('ry')) { // 皇家單
                  for (let i in res.data.result.ry) {
                    res.data.result.ry[i].vendor = 'ry'
                    items.push(res.data.result.ry[i])
                  }
                }
                if (res.data.result.hasOwnProperty('peParlay')) { // 體彩過關單
                  for (let i in res.data.result.peParlay) {
                    res.data.result.peParlay[i].vendor = 'pe'
                    items.push(res.data.result.peParlay[i])
                  }
                }
                let id = 0
                for (let i in items) {
                  // 加入 id 資料
                  id = id + 1
                  items[i].id = id
                }
                // 更新搜尋後的表格資料
                this.tableList.contents = items

                // 更新表格上方搜尋資訊
                this.searchInfo.itemsOfVendor = res.data.result.itemsOfVendor
                this.searchInfo.gameType = res.data.result.gameType
                this.searchInfo.playType = res.data.result.playType
                this.searchInfo.showTime = res.data.result.showTime

                // 各合計資料
                this.tableList.totals.betAmount = this.countTotal(items, 'cmount')
                this.tableList.totals.effectiveBet = this.countTotal(items, 'bmount')
                this.tableList.totals.member.g0_comm = this.countTotal(items, 'g0_comm')
                this.tableList.totals.member.g0_mount = this.countTotal(items, 'g0_mount')
                this.tableList.totals.agent.g1_comm = this.countTotal(items, 'g1_comm')
                this.tableList.totals.agent.g1_mount = this.countTotal(items, 'g1_mount')
                this.tableList.totals.agent.g1_result = this.countTotal(items, 'g1_result')
                this.tableList.totals.generalAgent.g2_comm = this.countTotal(items, 'g2_comm')
                this.tableList.totals.generalAgent.g2_mount = this.countTotal(items, 'g2_mount')
                this.tableList.totals.generalAgent.g2_result = this.countTotal(items, 'g2_result')
                this.tableList.totals.shareholder.g3_comm = this.countTotal(items, 'g3_comm')
                this.tableList.totals.shareholder.g3_mount = this.countTotal(items, 'g3_mount')
                this.tableList.totals.shareholder.g3_result = this.countTotal(items, 'g3_result')
                this.tableList.totals.majorShareholder.g4_comm = this.countTotal(items, 'g4_comm')
                this.tableList.totals.majorShareholder.g4_mount = this.countTotal(items, 'g4_mount')
                this.tableList.totals.majorShareholder.g4_result = this.countTotal(items, 'g4_result')
                this.tableList.totals.director.g5_comm = this.countTotal(items, 'g5_comm')
                this.tableList.totals.director.g5_mount = this.countTotal(items, 'g5_mount')
                this.tableList.totals.director.g5_result = this.countTotal(items, 'g5_result')
                this.tableList.totals.majorDirector.g6_comm = this.countTotal(items, 'g6_comm')
                this.tableList.totals.majorDirector.g6_mount = this.countTotal(items, 'g6_mount')
                this.tableList.totals.majorDirector.g6_result = this.countTotal(items, 'g6_result')
              } else {
                console.error('response 200 other error: ', res.data)
                // 跳轉回搜尋頁面
                this.toBack()
              }
            }
            // 載入狀態(結束)
            this.loadingStatus = false
          })
          .catch(err => {
            console.error(err)
            // 載入狀態(結束)
            this.loadingStatus = false
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo && dashboardInfo.hasOwnProperty('noOpenGame')) {
        // 不開放遊戲設定
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }
        // 有開放 BingoStar
        if (dashboardInfo.noOpenGame.indexOf('s16') === -1) {
          this.openGameType('s16')
        }
        // 有開放 Super彩球
        if (dashboardInfo.noOpenGame.indexOf('s17') === -1) {
          this.openGameType('s17')
        }
        // 有開放皇家
        if (dashboardInfo.noOpenGame.indexOf('s18') === -1) {
          this.openGameType('s18')
        }
      }
    },
    // 開放遊戲
    openGameType (type) {
      switch (type) {
        // 開放BingoStar
        case 's16':
          if (common.isObject(this.$route.query.searchItems)) {
            // [BingoStar]館別/項目
            this.searchItems.bg_game = this.$route.query.searchItems.bg_game
          }
          break
        // 開放Super彩球
        case 's17':
          if (common.isObject(this.$route.query.searchItems)) {
            // [Super彩球]館別/項目
            this.searchItems.lt_game = this.$route.query.searchItems.lt_game
          }
          break
        // 開放皇家視訊
        case 's18':
          if (common.isObject(this.$route.query.searchItems)) {
            // [皇家]館別/項目
            this.searchItems.ry_game = this.$route.query.searchItems.ry_game
          }
          break
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      switch (type) {
        // 關閉BingoStar
        case 's16':
          for (let i in this.gameHallItems.options) {
            if (this.gameHallItems.options[i].type === 'bingo') {
              this.gameHallItems.options[i].show = false
            }
          }
          break
        // 關閉Super彩球
        case 's17':
          for (let i in this.gameHallItems.options) {
            if (this.gameHallItems.options[i].type === 'lottery') {
              this.gameHallItems.options[i].show = false
            }
          }
          break
        // 關閉皇家視訊
        case 's18':
          for (let i in this.gameHallItems.options) {
            if (this.gameHallItems.options[i].type === 'royal') {
              this.gameHallItems.options[i].show = false
            }
          }
          break
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
